'use client';
import { useState, useEffect } from 'react';
import ProductList from '../components/ProductList';
import Cart from '../components/Cart';
import OrderSummary from '../components/OrderSummary';

const API_PRODUCTS_URL = 'http://oms-backend.test/api/products';
const API_ORDERS_URL = 'http://oms-backend.test/api/orders';

export default function Home() {
  const [products, setProducts] = useState([]);
  const [cart, setCart] = useState([]);
  const [orderNumber, setOrderNumber] = useState(null);
  const [orderStatus, setOrderStatus] = useState(null);
  const [refreshKey, setRefreshKey] = useState(0);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // Fetch products
  useEffect(() => {
    const fetchProducts = async () => {
      try {
        setLoading(true);
        const response = await fetch(API_PRODUCTS_URL);
        if (!response.ok) throw new Error('Failed to fetch products');
        const data = await response.json();
        setProducts(data);
      } catch (err) {
        console.error('Error fetching products:', err);
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };
    fetchProducts();
  }, []);

  const addToCart = (product, quantity) => {
    setCart((prev) => {
      const existing = prev.find((item) => item.product_id === product.id);
      if (existing) {
        return prev.map((item) =>
          item.product_id === product.id
            ? { ...item, quantity: item.quantity + quantity }
            : item
        );
      } else {
        return [
          ...prev,
          {
            product_name: product.name,
            product_id: product.id,
            unit_price: parseFloat(product.price),
            quantity,
          },
        ];
      }
    });
  };

  const updateCartItem = (id, quantity) => {
    if (quantity <= 0) setCart(cart.filter((i) => i.product_id !== id));
    else setCart(cart.map((i) => (i.product_id === id ? { ...i, quantity } : i)));
  };

  const calculateTotal = () =>
    cart.reduce((t, i) => t + i.unit_price * i.quantity, 0);

  const createOrder = async () => {
    if (cart.length === 0) return alert('Cart is empty');

    const payload = {
      total_amount: calculateTotal(),
      status: 'created',
      items: cart,
    };

    try {
      const response = await fetch(API_ORDERS_URL, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload),
      });

      if (!response.ok) throw new Error('Failed to create order');
      const data = await response.json();
      setOrderNumber(data.data.order_number);
      setOrderStatus('created');
      alert('Order created successfully!');

      // ✅ Trigger refresh after new order
      setRefreshKey((prev) => prev + 1);
      setCart([]);
    } catch (err) {
      console.error('Error creating order:', err);
      alert(`Error: ${err.message}`);
    }
  };

  const checkout = async () => {
    if (!orderNumber) return alert('Please create an order first');
    const payload = { order_number: orderNumber, status: 'confirmed' };
    try {
      const response = await fetch(API_ORDERS_URL, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload),
      });
      if (!response.ok) throw new Error('Failed to confirm order');
      setOrderStatus('confirmed');
      alert('Order confirmed!');
      // ✅ Trigger refresh after confirmation
      setRefreshKey((prev) => prev + 1);
    } catch (err) {
      console.error('Error confirming order:', err);
      alert(`Error: ${err.message}`);
    }
  };

  if (loading) return <div>Loading products...</div>;
  if (error) return <div className="text-red-500">Error: {error}</div>;

  return (
    <div className="container mx-auto p-4">
      <h1 className="text-3xl font-bold mb-8">E-Commerce Store</h1>
      <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
        <div className="col-span-5">
          <h2 className="text-2xl font-semibold mb-4">Products</h2>
          <ProductList products={products} addToCart={addToCart} />
        </div>
        <div className="col-span-7">
          <h2 className="text-2xl font-semibold mb-4">Cart</h2>
          <Cart
            cart={cart}
            updateCartItem={updateCartItem}
            total={calculateTotal()}
            orderStatus={orderStatus}
            createOrder={createOrder}
            checkout={checkout}
          />
        </div>
        <div className="col-span-12">
          {/* ✅ Auto-refresh summary when refreshKey changes */}
          <OrderSummary refreshTrigger={refreshKey} />
        </div>
      </div>
    </div>
  );
}
